import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { useWindowSize } from "../../utils/hooks";

const Wrapper = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.8;
  cursor: pointer;
  transform: ${props => {
    return `translate(${props.xp}px, ${props.yp}px)`;
  }};

  &:hover {
    opacity: 0.9;
  }
`;

const Widget = ({ children }) => {
  const [xp, setXp] = useState(0);
  const [yp, setYp] = useState(0);
  const [xratio, setXratio] = useState(0);
  const [yratio, setYratio] = useState(0);
  const [drag, setDrag] = useState(false);
  const [org, setOrg] = useState({
    clientX: 0,
    xratio: 0
  });
  const size = useWindowSize();

  const test = e => {
    console.log(e);
    setDrag(true);
    setOrg({
      clientX: e.clientX || e.touches[0].clientX,
      xratio: xratio,
      clientY: e.clientY || e.touches[0].clientY,
      yratio: yratio
    });
  };

  const move = e => {
    if (drag) {
      const player_wrap = document.getElementById("player_wrap");
      const diffX =
        ((e.clientX || e.touches[0].clientX) - org.clientX) /
        player_wrap.clientWidth;
      setXratio(org.xratio + diffX);
      setXp(player_wrap.clientWidth * (org.xratio + diffX));

      const diffY =
        ((e.clientY || e.touches[0].clientY) - org.clientY) /
        player_wrap.clientWidth;
      setYratio(org.yratio + diffY);
      setYp(player_wrap.clientWidth * (org.yratio + diffY));
    }
  };

  const up = e => {
    setDrag(false);
  };

  useEffect(() => {
    const player_wrap = document.getElementById("player_wrap");
    setXp(player_wrap.clientWidth * xratio);
    setYp(player_wrap.clientWidth * yratio);
  }, [size]);

  return (
    <Wrapper
      onMouseDown={test}
      onTouchStart={test}
      onMouseMove={move}
      onTouchMove={move}
      onMouseUp={up}
      onTouchEnd={up}
      xp={xp}
      yp={yp}
    >
      {children}
    </Wrapper>
  );
};

export default Widget;
