import React from "react";
import Widget from "./Widget";
import styled from "styled-components";

const Time = styled.div`
  color: white;
  font-family: "Baloo Bhai", cursive;

  font-size: 4vw;
`;

const ClockWidget = () => {
  return (
    <Widget>
      <Time>AM 07:30</Time>
    </Widget>
  );
};

export default ClockWidget;
